.main-container {
    background-color: var(--app-color-white-lightest);
    display: flex;
    width: 100%;
    height: 100vh;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: stretch;
    position: relative;
    flex: auto;
}

@media only screen and (max-width: 1068px) {
    .main-container {
        flex-direction: column;
    }
}

@media only screen and (min-width: 1068px) {
    .main-container {
        flex-direction: row;
    }
}
.first-container {
    margin: 0 0 0 30px;
    width: 534px;
    align-self: flex-start;
    display: flex;
    flex: auto;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    background-color: var(--app-color-white-lightest);
}
.second-container {
    background-color: var(--app-color-white-lightest);
    width: 50%;
    padding-left: 40px;

    width: 534px;
    height: 100%;
    min-height: 300px;
    flex: auto;
    //background-color: #f1f1f1;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
}

.left-20 {
    padding-left: 15%;
}

.logo-container {
    display: flex;
    padding-top: 30px;
}

.logo-size {
    width: 25px;
    height: 25px;
}

.main-form {
    position: relative;
    top: 20vh;
}

.input-style {
    font-family: var(--app-text-main-font), serif !important;
    font-size: 16px !important;
    line-height: 20px !important;
    color: #4a4a4a !important;
    height: 50px;
    width: 400px;
}

.login-btn {
    height: 50px;
    width: 100px;
    border-radius: 8px;
    border: 0px;
    background-color: #4a4a4a;
}

.btn-primary:hover {
    background-color: #333333;
}

input[type="text"] {
    padding: 2px;
}

input[type="password"] {
    padding: 2px;
}

.logo-text-style {
    font-family: var(--app-text-main-font);
    font-size: 24px;
    line-height: 24px;
    text-align: left;
    color: #4a4a4a;
}

.login-text-style {
    font-family: var(--app-text-main-font);
    font-size: 32px;
    line-height: 40px;
    text-align: left;
    color: #4a4a4a;
}

.alert {
    width: 350px !important;
    margin-top: 30px;
}

.terms-and-cond-text-style {
    font-family: var(--app-text-main-font);
    font-size: 16px;
    line-height: 22px;
    color: var(--app-color-gray-light);
}

.remember-me {
    font-family: var(--app-text-main-font);
    font-size: 16px;
    line-height: 22px;
    color: #333333;
    padding-left: 10px;
    padding-top: 2px;
}

.bold {
    color: #4a4a4a;
}

.login-btn span {
    font-family: var(--app-text-main-font);
    font-size: 16px;
    line-height: 20px;
    color: #ffffff;
}

@media (max-width: 836px) {
    .main-container {
        background-color: var(--app-color-white-lightest);
    }
    .need-help-main-container {
        background-color: #f1f1f1 !important;
    }
    .left-20 {
        padding-left: 2%;
    }
    .second-container {
       // width: 100%;
    }
    .second-container-content {
        margin-left: 0;
    }
}

.showHideButton .MuiTypography-body1 {
    font-family: var(--app-text-main-font), serif;
    font-size: 16px;
    color: var(--app-color-gray-light);
    cursor: pointer;
}
#my-input {
    font-family: var(--app-text-main-font), serif;
    font-size: 16px;
    line-height: 20px;
    color: #4a4a4a !important;
    padding-left: 10px;
    height: 50px;
    width: 400px;
}

.labelTextStyle {
    font-family: var(--app-text-main-font);
    font-size: 1.1rem;
    line-height: 22px;
    color: var(--app-color-gray-light);
    margin: 1px 0 2px 0;
    padding: 0 0 0 1px;
}
